import Step from "../../base/step/step.vue"
import Scroll from '../../base/scroll/scroll.vue'
import Upload from '../../base/upload/upload.vue'
export default {
  name: "textImg",
  components: {
    Step,
    Scroll,
    Upload
  },
  data() {
    return {
      isOk: true,
      keyWords: '',
      goodsImg: [],
      goodsObj: {},
      shopName: '',
      goodCommon: '',
      rbObj: {}
    }
  },
  created() {
    //
    let that = this;
    this.$axios.post('/api/orderOperate/getAdditionalInfo', {
      'buyerTaskRecordId': that.$route.query.buyerTaskRecordId
    }).then((data) => {
      console.log(data)
      if (data.data.code === '200') {
        this.goodsObj = data.data.data
        this.shopName = this.goodsObj.shopName
        this.$nextTick(() => {
          this.$refs.scroll.refresh()
        })
      } else {
        this.$vux.alert.show({
          title: '',
          content: data.data.message,
        })
      }
    }).catch(function (err) {
      console.log(err)
    });
    if (this.$route.query.rb) {
      //
      this.$axios.post('/api/orderOperate/getTaskRecordByOrderId', {
        'orderId': this.$route.query.buyerTaskRecordId
      }).then((data) => {
        if (data.data.code == '200') {
          this.rbObj = data.data.data
          this.goodCommon = this.rbObj.favorText
          this.goodsImg = JSON.parse(this.rbObj.favorCheckId)
          this.$nextTick(() => {
            this.$refs.scroll.refresh()
          })
        } else {
          this.$vux.alert.show({
            title: '',
            content: data.data.message,
          })
        }
      }).catch(function (err) {
        console.log(err)
      });
    }
  },
  watch: {
    goodsImg: {
      handler(val) {
        if (val.length > 3) {
          this.$nextTick(() => {
            this.$refs.scroll.refresh()
          })
        }
      },
      deep: true
    }
  },
  methods: {
    doNext() {
      let that = this;
      if (this.goodCommon !== '' && this.goodsImg.length > 2) {
        this.$axios.post('/api/orderOperate/backOrderSubmit', {
          buyerTaskRecordId: that.$route.query.buyerTaskRecordId,
          favorText: this.goodCommon,
          favorCheckId: this.goodsImg
        }).then((data) => {
          console.log(data)
          if (data.data.code === '200') {
            this.$router.push({ name: 'submitSuccess', query: { type: 'evaluate1' } })
          } else {
            this.$vux.alert.show({
              title: '',
              content: data.data.message,
            })
          }
        }).catch((error) => {
          this.$vux.alert.show({
            title: '',
            content: '',
          })
        })
      } else {
        this.$vux.alert.show({
          title: '',
          content: '!',
        })
      }
    }
  }
}



